.btn[type="submit"].is-animate {
  width: 74px;
  width: calc(36px + (74 - 36) * ((100vw - 320px) / (1024 - 320)));
  color: transparent;
  border-radius: 50px;
  background-color: #59D547;
  animation: submitAnim .3s ease-in-out alternate forwards;
  animation-delay: .3s;
}

