.btn__submit-check {
  position: absolute;
  top: 50%;
  left: 50%;
  margin-top: -16px;
  margin-top: calc(-7px + (-16 - -7) * ((100vw - 320px) / (1024 - 320)));
  margin-left: -18px;
  margin-left: calc(-10px + (-18 - -10) * ((100vw - 320px) / (1024 - 320)));
  width: 34px;
  width: calc(18px + (34 - 18) * ((100vw - 320px) / (1024 - 320)));
  height: auto;
  stroke-dasharray: 80;
  stroke-dashoffset: 80;
  transition: stroke-dashoffset .3s ease-in-out;
  transform: scale(1) rotateY(-180deg);
}

@media (max-width: 320px) {
